.app__header {
    background-color: var(--color-black);
    padding: 3rem;
}

.app__header-h1 {
    font-family: var(--font-base);
    color: var(--color-golden);
    font-weight: 700;
    letter-spacing: 0.04em;
    text-transform: capitalize;
    line-height: 117px;
    font-size: 90px;
}
.app__wrapper_info {
    width: 100%;
    overflow-x: auto;
}

.app__header-img img {
    width: 80%;
}


 